.app__footer{
    width: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: center;
    background: black;

    padding-top: 0;
}

.app__footer-links{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    margin-top: 5rem;
    padding: 0 2rem;
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work{
    flex: 1;
    margin: 1rem;
    text-align: center;
}

.app__footer-links_logo img:first-child{
    width: 340px;
    margin-bottom: 0.75rem;
}

.app__footer-links_work p:nth-child(2n + 1){
    margin-bottom: 1rem;
}

.app__footer-links_icons{
    margin-top: 0.5rem;
}

.app__footer-links_icons svg{
    color: white;
    margin: 0.5rem;
    font-size: 24px;
    cursor: pointer;
}

.app__footer-links_icons svg:hover{
    color: orange
}

.app__footer-headtext{
    color: white;
    padding-bottom: 1rem;
    font-size: 32px;
    font-family: var(--font-base);
    font-weight: 400px;
    letter-spacing: 0.04em;
    line-height: 41.6px;
}

.footer__copyright{
    margin-top: 5rem;
}

@media screen and (min-width: 2000px) {
    .app__footer-headtext {
        font-size: 51px;
        line-height: 61.6px;
    }
}

@media screen and (max-width: 1150px) {
    .app__footer-links {
        align-items: center;
        flex-direction: column;
        padding: 0;
    }

    .app__footer-links_contact,
    .app__footer-links_logo,
    .app__footer-links_work {
        margin: 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app__footer {
        padding: 0 0 2rem 0;
    }
}

@media screen and (max-width: 350px) {
    .app__footer-links_logo img:first-child {
        width: 80%;
    }
}
