.app__aboutus{
    position: relative;
}

.app__aboutus-overlay{
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img{
    width: 1000px;
    z-index: 0;
    opacity: 0.25;
}

.app__aboutus-content{
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about{
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: right;
}

.app__aboutus-content_knife{
    margin: 2rem 4rem;
}

.app__aboutus-content_knife img{
    height: 650px;
}

.app__aboutus-content_history{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
}

.app__aboutus-content_about h1,
.app__aboutus-content_history h1{
    color: orange;
}

.app__aboutus-content_about p,
.app__aboutus-content_history p{
    color: white;
    margin: 2rem 0;
}

@media  screen and (max-width: 2000px) {
    .app__aboutus-content_knife img{
        height: 950px;
    }

    .app__aboutus-content_about p,
    .app__aboutus-content_history p{
        margin: 4rem 0;
        color: white;
    }
}

@media  screen and (max-width: 950px) {
    .app__aboutus-content{
        flex-direction: column;
    }

    .app__aboutus-content_knife{
        margin: 4rem 0;
    }

    .app__aboutus-overlay img{
        width: 70%;
        height: 520px;
    }
}

@media  screen and (max-width: 650px) {
    .app__aboutus-overlay img{
        width: 75%;
        height: 250px;
    }
    
}